import React, { useEffect, useState } from 'react'
import app from '../../../../firebase';

function Blocked_u() {
    const[list, setList] = useState([]);
    useEffect(() => {
        app.database().ref("block/").on("value", snapshot => {
            let memberlist = [];
            snapshot.forEach(snap => {
                memberlist.push(snap.val());
            });
            setList(memberlist)
          });
    }, [])
    return (
        <div>
        <h2>Active Users</h2>
             <table id="example" class="display table">
            <thead class="thead-dark">
                <tr>
                    <th>Username</th>
                    <th>Role</th>
                    <th>Actions</th>
                </tr>
            </thead>
            <tbody>
            {list.map(data => {
                
                return (
                    <tr>     
                    <td>{data.username}</td>
                    <td>{data.type}</td>
                    <td>
                    <button style={{color:"green",margin:"1rem"}} onClick={()=>{
                       app.database().ref("users/"+data.uid).set({
                           email: data.email,
                           username: data.username,
                           type: data.type,
                           uid: data.uid
                       })
                       
                       app.database().ref("block/"+data.uid).remove()
                    }}>Unblock user</button></td>
                    </tr>
                    
                );
               
                })}
        
               
            </tbody>
            
         </table>
        </div>
    )
}

export default Blocked_u
