import React, { useEffect, useState } from 'react'
import AliceCarousel from 'react-alice-carousel'
import Footer from './Footer/Footer'
import NavHome from './Navbar/NavHome'
import blog1 from "./Images/blog1.jpg"
import blog2 from "./Images/blog2.jpg"
import "./Form.css"
import app from '../firebase'
import { useNavigate } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import Modal from 'react-modal';

const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
  };
function Blogs() {
    const[list, setList] = useState([]);
    const history = useNavigate()
    const { currentUser } = useAuth()
    const [modalIsOpen, setIsOpen] = useState(false);
    const [post_id, setPostId] = useState("")
    var Uid = currentUser.uid;
    const [admin, setAdmin] = useState(false)
    useEffect(() => {
        app.database().ref("blog/").orderByChild("likes").on("value", snapshot => {
          let memberlist = [];
          snapshot.forEach(snap => {
              memberlist.push(snap.val());
          });
          setList(memberlist)
        });
        app.database().ref().child('users/'+Uid+'/type').on('value', snapshot => { 
          if (snapshot.val()=="admin") {
              setAdmin(true)
          }     
        })
        
      }, [])
      function afterOpenModal() {
           
      }
    
      function closeModal() {
        setIsOpen(false);
      }
      function openModal() {
          setIsOpen(true);
        }
        function handleSubmit() {
          app.database().ref("blog/"+post_id).remove()
          setIsOpen(false);
        }
    return (
        <div>
        <NavHome></NavHome>
              <AliceCarousel autoPlay autoPlayInterval="3000" infinite disableButtonsControls>
      <img src={blog1} className="sliderimg"/>
      <img src={blog2} className="sliderimg"/>
</AliceCarousel>
<div>
<h2 style={{textAlign:"center"}}>Most Popular</h2>
{list.map(data => {
                
                return (
                   <div className="dashboard-pro" style={{margin:"2rem auto"}} >
                   <div>
                    {
        admin ?
        <i class="fas fa-trash-alt" style={{color:"red"}} onClick={()=>{
    setPostId(data.id)
    openModal()
}}></i>
        :
        <></>
    }
                    </div>
                   <img src={data.img || 'http://via.placeholder.com/400x300'} alt="logo" width="100" height="100" style={{display:"inline-block",margin:"0 1rem"}}
                    onClick={()=>{{history("/blog_view/"+data.id)}}}/>
                   <div style={{display:"inline-block", margin:"0 4rem", verticalAlign:"top"}} onClick={()=>{{history("/blog_view/"+data.id)}}}>
                   <p style={{ marginTop:"1rem"}} onClick={()=>{{history("/blog_view/"+data.id)}}}><b>{data.title}</b></p>
                   <p style={{marginTop:"1rem"}}  onClick={()=>{{history("/blog_view/"+data.id)}}}>By {data.author} {data.date}</p>
                   <p style={{textAlign:"left",width:"700px"}} onClick={()=>{{history("/blog_view/"+data.id)}}}>{data.text}</p>    
                   </div>
                   </div>
                );
               
                })}
</div>
<Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal">
      <h1>Are you sure?</h1>
    <button className='with-btn' onClick={handleSubmit}>Yes</button>
        <button  className='with-btn' onClick={closeModal}>No</button>
      </Modal>
<Footer/>
        </div>
    )
}

export default Blogs
