import React, { useState } from 'react'
import BarChart from 'react-bar-chart';


function Dash() {
    const margin = {top: 20, right: 20, bottom: 30, left: 40};
    const data = [
        {text: 'JAN', value: 0}, 
        {text: 'FEB', value: 0}, 
        {text: 'MAR', value: 0}, 
        {text: 'APR', value: 0}, 
        {text: 'MAY', value: 0}, 
        {text: 'JUN', value: 0}, 
        {text: 'JUL', value: 0}, 
        {text: 'AUG', value: 0}, 
        {text: 'SEP', value: 0}, 
        {text: 'OCT', value: 0}, 
        {text: 'NOV', value: 0}, 
        {text: 'DEC', value: 0}, 
      ];
    return (
        <div>
            <h2>Statistics</h2>
            <BarChart ylabel='Registrations'
                  width={600}
                  height={500}
                  margin={margin}
                  data={data}
                  />
            <BarChart ylabel='Posts'
                  width={600}
                  height={500}
                  margin={margin}
                  data={data}
                  />
        </div>
    )
}

export default Dash
