import React, { useEffect, useState } from 'react'
import NavHome from './Navbar/NavHome'
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";
import img1 from "./Images/img1.jpg"
import img2 from "./Images/img2.jpg"
import img3 from "./Images/img3.jpg"
import img4 from "./Images/img4.jpg"
import img5 from "./Images/img5.jpg"
import mar1 from "./Images/mar1.jpg"
import mar2 from "./Images/mar2.jpg"
import mar3 from "./Images/mar3.jpg"
import port1 from "./Images/port1.jpg"
import port2 from "./Images/port2.jpg"
import port3 from "./Images/port3.jpg"
import fam1 from "./Images/fam1.jpg"
import child1 from "./Images/child1.jpg"
import nature from "./Images/nature1.jpg"
import event from "./Images/event.jpg"
import comm from "./Images/comm.jpg"
import "./Form.css"
import Footer from './Footer/Footer';
import { useNavigate } from 'react-router-dom';
import app, { storage } from '../firebase';
import { useAuth } from '../contexts/AuthContext';
import Modal from 'react-modal';

const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
  };
function Home() {
    const history = useNavigate()
    const { currentUser } = useAuth()
    const [modalIsOpen, setIsOpen] = useState(false);
    var Uid = currentUser.uid;
    const [admin, setAdmin] = useState(false)
    const [image, setImage] = useState(null)
    const [imagef, setImagef] = useState(null)
    const [type_img, setImageType] = useState("")
    var key = app.database().ref().push().key;
    const [imag1, setImg1] = useState(null)
    const [imag2, setImg2] = useState(null)
    const [imag3, setImg3] = useState(null)
    const [imag4, setImg4] = useState(null)
    const [imag5, setImg5] = useState(null)
    const [imag6, setImg6] = useState(null)
    const [imag7, setImg7] = useState(null)
    const [imag8, setImg8] = useState(null)
    const [imag9, setImg9] = useState(null)
    const [imag10, setImg10] = useState(null)

    const [simag1, setSImg1] = useState(null)
    const [simag2, setSImg2] = useState(null)
    const [simag3, setSImg3] = useState(null)
    const [simag4, setSImg4] = useState(null)
    const [simag5, setSImg5] = useState(null)
    const [simag6, setSImg6] = useState(null)
    const [simag7, setSImg7] = useState(null)
    const [simag8, setSImg8] = useState(null)

    function afterOpenModal() {
           
    }
  
    function closeModal() {
      setIsOpen(false);
    }
    const onImagechange= (event) =>
    {
        if (event.target.files && event.target.files[0]) {
            setImage(URL.createObjectURL(event.target.files[0]));
            setImagef(event.target.files[0])
          }
    }

    async function handleSubmit2(e){
      e.preventDefault();
 
      const uploadTask = storage.ref(`images/${key}.png`).put(imagef);
    uploadTask.on('state_changed', 
    (snapshot) => {
      // progrss function ....
      const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
    }, 
    (error) => {
         // error function ....
      console.log(error);
    
    }, 
  () => {
      // complete function ....
      storage.ref('images').child(key+".png").getDownloadURL().then(url => {
        app.database().ref().child('feature/').set({
            type_img:url
        })
      })
      
  });
     
      
    }
    function openModal() {
      setIsOpen(true);
    }
    useEffect(() => {
        app.database().ref().child('users/'+Uid+'/type').on('value', snapshot => { 
          if (snapshot.val()=="admin") {
              setAdmin(true)
          }     
        })
        app.database().ref().child('feature/img1').on('value', snapshot => { 
           setImg1(snapshot.val())
          })
        app.database().ref().child('feature/img2').on('value', snapshot => { 
        setImg2(snapshot.val())
        })
        app.database().ref().child('feature/img3').on('value', snapshot => { 
        setImg3(snapshot.val())
        })
        app.database().ref().child('feature/img4').on('value', snapshot => { 
        setImg4(snapshot.val())
        })
        app.database().ref().child('feature/img5').on('value', snapshot => { 
        setImg5(snapshot.val())
        })
        app.database().ref().child('feature/img6').on('value', snapshot => { 
        setImg6(snapshot.val())
        })
        app.database().ref().child('feature/img7').on('value', snapshot => { 
        setImg7(snapshot.val())
        })
        app.database().ref().child('feature/img8').on('value', snapshot => { 
        setImg8(snapshot.val())
        })
        app.database().ref().child('feature/img9').on('value', snapshot => { 
        setImg9(snapshot.val())
        })
        app.database().ref().child('feature/img10').on('value', snapshot => { 
        setImg10(snapshot.val())
        })
        app.database().ref().child('feature/simg1').on('value', snapshot => { 
            setSImg1(snapshot.val())
           })
         app.database().ref().child('feature/simg2').on('value', snapshot => { 
         setSImg2(snapshot.val())
         })
         app.database().ref().child('feature/simg3').on('value', snapshot => { 
         setSImg3(snapshot.val())
         })
         app.database().ref().child('feature/simg4').on('value', snapshot => { 
         setSImg4(snapshot.val())
         })
         app.database().ref().child('feature/simg5').on('value', snapshot => { 
         setSImg5(snapshot.val())
         })
         app.database().ref().child('feature/simg6').on('value', snapshot => { 
         setSImg6(snapshot.val())
         })
         app.database().ref().child('feature/simg7').on('value', snapshot => { 
         setSImg7(snapshot.val())
         })
         app.database().ref().child('feature/simg8').on('value', snapshot => { 
         setSImg8(snapshot.val())
         })
    }, [])
    return (
        <div>
            <NavHome/>
            <div class="container">
                <AliceCarousel autoPlay autoPlayInterval="3000" infinite disableButtonsControls className="img">
      <img src={img1} className="sliderimg"/>
      <img src={img2} className="sliderimg"/>
      <img src={img3} className="sliderimg"/>
      <img src={img4} className="sliderimg"/>
      <img src={img5} className="sliderimg"/>
</AliceCarousel> 
<h1 className="txt">Best photographers at your fingertips</h1>
 <button className="btn" onClick={()=>{history("/book")}}>Book Now</button>
            </div>
           
    <div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("img1")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag1||mar1} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img2")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag2||port1} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img3")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag3||mar2} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img4")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag4||mar3} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img5")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag5||port2} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img6")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag6||fam1} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img7")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag7||child1} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img8")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag8||port3} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img9")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag9||img4} width="250" height="200"/>
        </div>
        <div style={{display:"inline-block"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit"  onClick={()=>{
                 setImageType("img10")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={imag10||img5} width="250" height="200"/>
        </div>
        
    </div>
<div>
    <p style={{fontSize:"20px"}}><b>Specialties</b></p>
    <div>
    <div style={{display:"inline-block",margin:"2rem"}}>
    <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg1")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag1||port1} width="250" height="200"/>
        <p>Portrait</p>
        </div>
        <div style={{display:"inline-block",margin:"2rem"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg2")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag2||event} width="250" height="200"/>
        <p>Event</p>
        </div>
        <div style={{display:"inline-block",margin:"2rem"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg3")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag3||nature} width="250" height="200"/>
        <p>Nature</p>
        </div>
        <div style={{display:"inline-block",margin:"2rem"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg4")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag4||mar3} width="250" height="200"/>
        <p>Wedding</p>
        </div>
        <div style={{display:"inline-block",margin:"2rem"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg5")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag5||comm} width="250" height="200"/>
        <p>Commercial</p>
        </div>
        <div style={{display:"inline-block",margin:"2rem"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg6")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag6||img3} width="250" height="200"/>
        <p>Fashion</p>
        </div>
        <div style={{display:"inline-block",margin:"2rem"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg7")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag7||port2} width="250" height="200"/>
        <p>Personal</p>
        </div>
        <div style={{display:"inline-block",margin:"2rem"}}>
        <div>
        {
            admin ?
            <i class="fas fa-edit" onClick={()=>{
                 setImageType("simg8")
                 openModal()}}></i>
            :
            <></>
        }
        </div>
        <img src={simag8||fam1} width="250" height="200"/>
        <p>Family</p>
        </div>
    </div>
    <p style={{fontSize:"20px"}}><b>Our Process</b></p>
    <p style={{fontSize:"10px"}}><b>3 easy steps!</b></p>
    <div>
        <div className='div-small' style={{display:"inline-block", margin:"2rem"}}>
        <i class="fas fa-search" style={{fontSize:"50px",margin:"0.1rem"}}></i>
        <p>Choose when, where and type of photoshoot you want</p>
        </div>
        <div className='div-small' style={{display:"inline-block", margin:"2rem"}}>
        <i class="fas fa-camera" style={{fontSize:"55px",margin:"0.1rem"}}></i>
        <p>Choose from a range of fantastic photographers on iPapz</p>
        </div>
        <div className='div-small' style={{display:"inline-block", margin:"2rem"}}>
        <i class="fas fa-smile-wink" style={{fontSize:"50px",margin:"0.1rem"}}></i>
        <p>Have your photoshoot!</p>
        </div>
    </div>
</div>
<Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal">
         <div >
    <div>
                <b>Featured Photo</b>
                <div style={{display:"inline", float:"right"}}>
                <input type="file" onChange={onImagechange}/>
            </div>
            </div>
            <img src={image || 'http://via.placeholder.com/200x100'} alt="logo" width="200" height="100"/>
            
    </div>
    <button className='with-btn' onClick={handleSubmit2}>Upload</button>
        <button  className='with-btn' onClick={closeModal}>close</button>
      </Modal>
        
<Footer/>
        </div>
    )
}

export default Home
