import React, { useCallback, useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import app from '../firebase';
import Footer from './Footer/Footer';
import NavHome from './Navbar/NavHome';
import { Controlled as ControlledZoom } from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'
import "./Form.css"

function Shopview() {
    let { Id } = useParams();
    const[img, setImg] = useState("");
    const[text, setText] = useState("");
    const[price, setPrice] = useState("");
    const[title, setTitle] = useState("");
    const[cart, setCart] = useState(1);
    const { currentUser } = useAuth()
    var Uid = currentUser.uid;
    const history = useNavigate()
    const[list, setList] = useState([]);
    useEffect(() => {
        app.database().ref("all/"+Id+"/img").on("value", snapshot => {
          setImg(snapshot.val())
         });
         app.database().ref("all/"+Id+"/desc").on("value", snapshot => {
          setText(snapshot.val())
         });
         app.database().ref("all/"+Id+"/price").on("value", snapshot => {
          setPrice(snapshot.val())
         });
         app.database().ref("all/"+Id+"/title").on("value", snapshot => {
          setTitle(snapshot.val())
         });
         app.database().ref("all/"+Id+"/url").on("value", snapshot => {
            let memberlist = [];
            snapshot.forEach(snap => {
                memberlist.push(snap.val());
            });
            setList(memberlist)
           });
    }, [])
    const [isZoomed, setIsZoomed] = useState(false)

  const handleImgLoad = useCallback(() => {
    setIsZoomed(true)
  }, [])
  const handleCart=()=>{
    app.database().ref("cart/"+Uid).child(Id).set({
      img: img,
      price: price,
      title: title,
      search: title.toUpperCase(),
      desc: text,
      id: Id,
      user_id:Uid,
      quant: cart
  })
  history("/cart")
  }

  const handleZoomChange = useCallback(shouldZoom => {
    setIsZoomed(shouldZoom)
  }, [])
  return (
<div>
<NavHome/>
<div style={{margin:"2rem"}}>
<div style={{display:"inline-block", marginRight:"2rem"}}>
<ControlledZoom isZoomed={isZoomed} onZoomChange={handleZoomChange}>
      <img
        alt="product"
        src={img}
        width="500"
      />
    </ControlledZoom>
    <div style={{overflow: "auto",
  whiteSpace: "nowrap"}}>
    {list.map(data => {
                
                return (
                   <div style={{display:"inline-block"}} onClick={()=>{
                     setImg(data)
                   }}>
                   <img src={data} style={{width:"5rem",height:"5rem"}}></img>
                   </div>
                    );
               
            })}
    </div>
</div>
<div style={{display:"inline-block",verticalAlign:"top"}}>
<h1>{title}</h1>
<div style={{display:"block"}}>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    </div>
<b>£{price}</b><button style={{margin:"0 1rem"}} className="form-inputlogin-btn"
onClick={handleCart}
>Add To Cart</button>
<i class="fas fa-plus" style={{cursor:"pointer"}} onClick={()=>{
  setCart(cart+1)
}}></i><b style={{padding:"0 1rem", textAlign:"center",margin:"0 1rem",border:"1px solid black"}}
>{cart}</b><i class="fas fa-minus"  style={{cursor:"pointer"}}
onClick={()=>{
  setCart(cart-1)
}}></i>
<p>{text}</p>
</div>
<hr></hr>
<b>You may also be interested in:</b>
</div>
<Footer/>
  </div>
  );
}

export default Shopview;
