import React, { useEffect, useState } from 'react'
import { useAuth } from '../contexts/AuthContext';
import app from '../firebase'
import Footer from './Footer/Footer'
import NavHome from './Navbar/NavHome'
import Modal from 'react-modal';
import { useNavigate } from 'react-router-dom';

const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
  };
function Shop() {
    const[list, setList] = useState([]);
    const[num, setNum] = useState(0);
    const [searchb, setSearch] = useState("")
    const { currentUser } = useAuth()
    const [modalIsOpen, setIsOpen] = useState(false);
    const [post_id, setPostId] = useState("")
    var Uid = currentUser.uid;
    const [admin, setAdmin] = useState(false)
    const history = useNavigate()

    useEffect(() => {
        app.auth().onAuthStateChanged(user => {
           
               app.database().ref("all/").orderByChild("search").startAt(searchb.toUpperCase())
               .endAt(searchb.toUpperCase()+"\uf8ff").on("value", snapshot => {
            let memberlist = [];
            snapshot.forEach(snap => {
                memberlist.push(snap.val());
            });
            setList(memberlist)
          });
          app.database().ref("all/").orderByChild("search").startAt(searchb.toUpperCase())
          .endAt(searchb.toUpperCase()+"\uf8ff").on("value", snapshot => {
           if (snapshot.exists) {
            setNum(snapshot.numChildren())
           }
           else
           {
            setNum(0)
           }
          });
            
           
        })
        app.database().ref().child('users/'+Uid+'/type').on('value', snapshot => { 
            if (snapshot.val()=="admin") {
                setAdmin(true)
            }     
          })
    }, [])
    function searchHandle(event)
    {
      
        app.database().ref("all/").orderByChild("search").startAt(event.target.value.toUpperCase())
      .endAt(event.target.value.toUpperCase()+"\uf8ff")
      .on("value", snapshot => {
        let memberlist = [];
        snapshot.forEach(snap => {
            memberlist.push(snap.val());
        });
        setList(memberlist)
      });
      event.preventDefault()

    }
    function afterOpenModal() {
           
    }
  
    function closeModal() {
      setIsOpen(false);
    }
    function openModal() {
        setIsOpen(true);
      }
      function handleSubmit() {
        app.database().ref("all/"+post_id).remove()
        setIsOpen(false);
      }
    return (
        <div>
            <NavHome/>
            <div>
            <div style={{display:"inline-block"}}>
            <input
                placeholder='search'
                type="text"
                style={{margin:"1rem"}}
                onChange={searchHandle}
            />
            <div style={{border:"0.5px solid black",margin:"1rem"}}>
            <h3>Price</h3>
            <p style={{margin:"1rem"}}>Up to $10</p>
            <p style={{margin:"1rem"}}>$10-$20</p>
            <p style={{margin:"1rem"}}>$20-$30</p>
            <p style={{margin:"1rem"}}>over $30</p>
            </div>
            <div style={{border:"0.5px solid black",margin:"1rem",width:"12rem"}}>
            <h3>Colors</h3>
            <p style={{margin:"1rem",color:"red",display:"inline-block"}}><i class="fas fa-square"></i></p>
            <p style={{margin:"1rem",color:"blue",display:"inline-block"}}><i class="fas fa-square"></i></p>
            <p style={{margin:"1rem",color:"black",display:"inline-block"}}><i class="fas fa-square"></i></p>
            <p style={{margin:"1rem",color:"green",display:"inline-block"}}><i class="fas fa-square"></i></p>
            <p style={{margin:"1rem",color:"wheat",display:"inline-block"}}><i class="fas fa-square"></i></p>
            <p style={{margin:"1rem",color:"purple",display:"inline-block"}}><i class="fas fa-square"></i></p>
            </div>
            <div style={{border:"0.5px solid black",margin:"1rem",width:"12rem"}}>
            <h3>Product Rating</h3>
            <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
            <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
            <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
            <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
            <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
           </div>
           <div style={{border:"0.5px solid black",margin:"1rem",width:"12rem"}}>
            <h3>Standard Photographic Sizes</h3>
            <p style={{margin:"1rem"}}>Print inches MM</p>
            <p style={{margin:"1rem"}}>4R 4X6 102X 152</p>
            <p style={{margin:"1rem"}}>5R 5X7 127X 178</p>
            <p style={{margin:"1rem"}}>6R 6X8 152X 203</p>
            <p style={{margin:"1rem"}}>8R 8X10 203X 254</p>
           </div>
            </div>
            <div style={{display:"inline-block", verticalAlign:"top",width:"80%"}}>
            {list.map(data => {
                
                return ( 
                    <div style={{display:"inline-block"}}>
<div>
{
admin ?
<i class="fas fa-trash-alt" style={{color:"red"}} onClick={()=>{
    setPostId(data.id)
    openModal()
}}></i>
:
<></>
}
</div>
                    <img
                        src={data.img}
                        width="200" height="200" style={{display:"block",margin:"2rem"}}
                        onClick={()=>{history("/shop_view/"+data.id)}}
                    />
                    <p style={{display:"block",margin:"0 2rem"}}  onClick={()=>{history("/shop_view/"+data.id)}}><b>{data.title}</b></p>
                    <p style={{display:"block",margin:"0rem 2rem"}}><b>£{data.price}</b></p>
                    <div style={{display:"block",margin:"0rem 2rem"}}>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    <p style={{display:"inline-block"}}><i class="far fa-star"></i></p>
                    </div>
                    </div>
                );
               
            })}

            </div>
            </div>
            <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal">
      <h1>Are you sure?</h1>
    <button className='with-btn' onClick={handleSubmit}>Yes</button>
        <button  className='with-btn' onClick={closeModal}>No</button>
      </Modal>
            <Footer/>
        </div>
    )
}

export default Shop
