import React, { useEffect, useState } from 'react';
import app from '../firebase';
import Footer from './Footer/Footer';
import NavHome from './Navbar/NavHome';


function Cart() {
    const[list, setList] = useState([]);
    const[num, setNum] = useState(0);
    const[user, setUser] = useState("");
    const[total, setTotal] = useState(0);
    useEffect(() => {
        app.auth().onAuthStateChanged(user => {
            if (user) {
              setUser(user)
               app.database().ref("cart/"+user.uid).on("value", snapshot => {
            let memberlist = [];
            snapshot.forEach(snap => {
                memberlist.push(snap.val());
            });
            setList(memberlist)
          });
          app.database().ref("cart/"+user.uid).on("value", snapshot => {
           if (snapshot.exists) {
            setNum(snapshot.numChildren())
           }
           else
           {
            setNum(0)
           }
          });
            }
           
        })
       
    }, [])
  return (
  <div>
  <NavHome/>
  <div style={{margin:"5rem 0"}}>
  <div style={{display:"inline-block",width:"70vw"}}>
   <table id="example" class="display table">
            <thead class="thead-dark">
                <tr>
                    <th>Product</th>
                    <th>Title</th>
                    <th>Quantity</th>
                    <th>Price</th>
                </tr>
            </thead>
            <tbody>
            {list.map(data =>{
               
                return (
                    <tr>     
                    <td>  
                    <img
                        
                        style={{width:"100px",height:"100px",alignSelf: 'center', margin:"5px"}}
                          src={data.img}
                         
                      /></td>
                    <td><p style={{textAlign: 'center',fontSize:"15px",fontWeight:"bold",margin:"5px"}}>{data.title}</p></td>
                    <td>  <i class="fas fa-minus" onClick={()=>{
                        var like = 0;
                        app.database().ref("cart/"+user.uid+"/"+data.id).child("quant").on("value", snapshot => {
                          like = snapshot.val()-1
                          });
                          app.database().ref("cart/"+user.uid).child(data.id).update({
                            quant: like
                          })
                        }}/>
                        <b style={{fontSize:15,fontWeight:"bold",marginRight:5,border:"1px solid black",width:19,textAlign:"center",padding:"0 1rem"}}>{data.quant}</b>
                        <i class="fas fa-plus" onClick={()=>{
                          var like = 0;
                        app.database().ref("cart/"+user.uid+"/"+data.id).child("quant").on("value", snapshot => {
                          like = snapshot.val()+1
                          });
                          app.database().ref("cart/"+user.uid).child(data.id).update({
                            quant: like
                          })
                        }}/></td>
                    <td><p style={{fontSize:"15px",fontWeight:"bold",margin:"5px"}}>£{data.price}</p></td>
                    <td> <i class="fas fa-trash-alt" style={{margin:10, color:"red",cursor:"pointer"}} onClick={()=>{
                           app.database().ref("cart/"+user.uid+"/"+data.id).remove()
                        }}/></td>
                    </tr>
                    
                );
               
                })}
        
               
            </tbody>
            
         </table>    
  </div>
 <div style={{display:"inline-block",verticalAlign:"top", marginLeft:"1rem",width:"20vw"}}>
 <label>
    First Name
     <input
         className='form-input'
         placeholder='First Name'
     />
 </label>
 <label  style={{display:"block"}}>
    Last Name
     <input
         className='form-input'
         placeholder='Last Name' 
     />
 </label>
 <label  style={{display:"block"}}>
    Company
     <input
         className='form-input'
         placeholder='Company' 
     />
 </label>
 <label  style={{display:"block"}}>
    Address
     <input
         className='form-input'
     />
      <input
         className='form-input'
     />
      <input
         className='form-input'
     />
 </label>
 <label  style={{display:"block"}}>
    City
     <input
         className='form-input'
         placeholder='City' 
     />
 </label>
 <label  style={{display:"block"}}>
    Postcode
     <input
         className='form-input'
         placeholder='Postcode' 
     />
 </label>
 <label  style={{display:"block"}}>
    Telephone
     <input
         className='form-input'
         placeholder='Telephone' 
     />
 </label>
 <button className="form-inputlogin-btn">Check Out</button>
 </div>
  </div>
  <Footer/>
  </div>
  );
}

export default Cart;
