import React from 'react'
import Footer from './Footer/Footer'
import NavHome from './Navbar/NavHome'

function Forum() {
    return (
        <div>
        <NavHome/>
        <div>
        <div style={{border:"1px solid black",height:"250px",width:"50rem",margin:"2rem auto"}}>
            <h1 style={{margin:"2rem"}}>Welcome to iPapz Forum!</h1>
            <p style={{margin:"2rem"}}>Ask questions, get answers and connect
with our community of members and
specialists.</p>

<input
                placeholder='search'
                type="text"
                style={{float:"right", margin:"0.5rem"}}
               
            />
        </div>
        <div style={{margin:"4rem"}}>
        <h3>Forum Activity</h3>
        
        <label>
        Sort by
            <select  value={{}} onChange={{}}>
              <option value="Most Recent">Most Recent</option>
              <option value="Most Viewed">Most Viewed</option>
              <option value="Most Liked">Most Liked</option>
            </select>
          </label>
        </div>
        
        </div>
         <Footer/>   
        </div>
    )
}

export default Forum
