import React, { useEffect, useRef, useState } from 'react'
import Modal from 'react-modal';
import { useAuth } from '../../../contexts/AuthContext';
import app, { storage } from '../../../firebase';

const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
  };
function Photo_up() {
    let subtitle;
    const [modalIsOpen, setIsOpen] = useState(false);
    const [modalIsOpen2, setIsOpen2] = useState(false);
    const { currentUser } = useAuth()
    var Uid = currentUser.uid;
    var key = app.database().ref().push().key;
    const authorRef = useRef()
    const textRef = useRef()
    const titleRef = useRef()
    const[list, setList] = useState([]);
    const [image, setImage] = useState(null)
    const [imagef, setImagef] = useState(null)
    const [loading, setLoading] = useState(false)
    var [name,setName] = useState("")
    let myCurrentDate = new Date()
let date = myCurrentDate.getDate();
let month = myCurrentDate.getMonth() + 1;
let year = myCurrentDate.getFullYear();

    useEffect(() => {
        app.database().ref("gallery/"+Uid).orderByChild("title").on("value", snapshot => {
          let memberlist = [];
          snapshot.forEach(snap => {
              memberlist.push(snap.val());
          });
          setList(memberlist)
        });
        app.database().ref().child('users/'+Uid+'/username').on('value', snapshot => {
            
          setName(snapshot.val()) 
           
      })
      }, [])
      async function postHandle(){
        if (imagef!=null) {
          const uploadTask = storage.ref(`images/${key}.png`).put(imagef);
          uploadTask.on('state_changed', 
          (snapshot) => {
            // progrss function ....
            const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
          }, 
          (error) => {
               // error function ....
            console.log(error);
            setLoading(false)
          }, 
        () => {
            // complete function ....
            storage.ref('images').child(key+".png").getDownloadURL().then(url => {
              app.database().ref("my_blog/"+Uid+"/"+key).set({
                img: url,
                title: titleRef.current.value,
                author:authorRef.current.value,
                text: textRef.current.value,
                name: name,
                user_id:Uid,
                id:key,
                date:`${year}-${month<10?`0${month}`:`${month}`}-${date}`
                })
                app.database().ref("blog/"+key).set({
                  img: url,
                  title: titleRef.current.value,
                  author:authorRef.current.value,
                  text: textRef.current.value,
                  name: name,
                  user_id:Uid,
                  id:key,
                  date:`${year}-${month<10?`0${month}`:`${month}`}-${date}`
                  })
                  setIsOpen2(true)
            setLoading(false)
            })
            
        });
        }
        else{
          app.database().ref("my_blog/"+Uid+"/"+key).set({
            img: image,
            title: titleRef.current.value,
            author:authorRef.current.value,
            text: textRef.current.value,
            name: name,
                  user_id:Uid,
                  id:key,
                  date:`${year}-${month<10?`0${month}`:`${month}`}-${date}`
            })
            app.database().ref("blog/"+key).set({
              img: image,
              title: titleRef.current.value,
              author:authorRef.current.value,
              text: textRef.current.value,
              name: name,
                  user_id:Uid,
                  id:key,
                  date:`${year}-${month<10?`0${month}`:`${month}`}-${date}`
              })
       
        setIsOpen2(true)
        setLoading(false)
        }
      }
    function openModal() {
      setIsOpen(true);
    }
  
    function afterOpenModal() {
      // references are now sync'd and can be accessed.
      subtitle.style.color = '#000';
    }
  
    function closeModal() {
      setIsOpen(false);
    }
    const onImagechange= (event) =>
    {
        if (event.target.files && event.target.files[0]) {
            setImage(URL.createObjectURL(event.target.files[0]));
            setImagef(event.target.files[0])
          }
    }
    return (
        <div style={{margin:"4rem"}}>
            <h2>Upload Blog</h2>
            <h3 style={{marginTop:"2rem"}}>Content</h3>
            <p style={{margin:"0.5rem 0"}}><b >Thumbnail Image</b></p>
            <img src={image|| 'http://via.placeholder.com/500x300'} alt="logo" width="500" height="300"/>
           <div>
           <label for="file-upload" className="custom-file-upload">
     Upload From Device
</label>
<input id="file-upload" type="file" className='img_up' onChange={onImagechange}/>
<button className="custom-file-upload" onClick={openModal}>Upload From Gallery</button>
           </div>
           <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h2 ref={(_subtitle) => (subtitle = _subtitle)}>Gallery</h2>
        <div>
        {list.map(data => {
                
                return (
                    <div style={{display:"inline-block", margin:"1rem 2rem",backgroundColor:"#ECECEC"}} onClick={()=>{setImage(data.img)
                    closeModal()
                    }}>
                   <img src={data.img || 'http://via.placeholder.com/300x200'} alt="logo" width="300" height="200" />
                   <p style={{textAlign:"center", marginTop:"1rem"}}><b>{data.title}</b></p>
                   </div>
                );
               
                })}
        </div>
        <button onClick={closeModal}>close</button>
      </Modal>
      <div style={{marginTop:"2rem"}}>
      <label className='form-label' >Text article</label>
          <textarea
            className='form-input'
            type='text'
            placeholder=' Enter Text Article'
            style={{height:"10rem"}}
           ref={textRef}
            required
          />
        <label className='form-label' >Author</label>
          <input
            className='form-input'
            type='text'
            placeholder=' Enter Author'
            ref={authorRef}
            required
          />
          <label className='form-label' >Title</label>
          <input
            className='form-input'
            type='text'
            placeholder=' Enter Title'
            ref={titleRef}
            required
          />
          <button className='with-btn' onClick={postHandle}>Post </button>
      </div>
      <Modal
        isOpen={modalIsOpen2}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h1>successful!!</h1>
      </Modal>
        </div>
    )
}

export default Photo_up
